import axios from 'axios'
import {Message} from 'element-ui'
import store from '../store'
import router from '../router'
axios.interceptors.request.use(config => {
  config.headers.Authorization= store.state.user.tokens;
  return config;
}, err => {
  Message.error({message: '请求超时!'});
  // return Promise.resolve(err);
})
axios.interceptors.response.use(data => {
  if ( data.data.code === "404") {
    store.commit('logout');
    router.replace({path: '/'});
     Message.error({message: data.data.msg});
    return;
  }
 /* if (data.data.msg) {
    Message.success({message: data.data.msg});
  }*/
  return data;
}, err => {
 /* if (err.response.code === 504 || err.response.code === 404) {
    Message.error({message: '服务器被吃了⊙﹏⊙∥'});
  } else if (err.response.code === 403) {
    Message.error({message: '权限不足,请联系管理员!'});
  } else if (err.response.code === 401) {
    Message.error({message: err.response.data.msg});
  } else {
    if (err.response.data.msg) {
      Message.error({message: err.response.data.msg});
    }else{
      Message.error({message: '未知错误!'});
    }
  }*/
  // return Promise.resolve(err);
});
let base = '/api';
export const postRequest = (url, params) => {
  return axios({
    method: 'post',
    url: `${base}${url}`,
    data: params,
    contentType:'application/x-www-form-urlencoded',
    transformRequest: [function (data) {
      let ret = '';
      for (let it in data) {
        ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
      }
      return ret
    }],
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  });
};

export const postRequestJson = (url, params) => {
  return axios({
    method: 'post',
    url: `${base}${url}`,
    data: params,
    contentType:'application/json',
    headers: {
      'Content-Type': 'application/json;charset=utf-8'
    }
  });
};

export const postRequestFile = (url, params) => {
  return axios({
    method: 'post',
    url: `${base}${url}`,
    data: params,
    responseType: 'arraybuffer',
  });
};

export const uploadFileRequest = (url, params) => {
  return axios({
    method: 'post',
    url: `${base}${url}`,
    data: params,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  });
};


export const putRequest = (url, params) => {
  return axios({
    method: 'put',
    url: `${base}${url}`,
    data: params,
    transformRequest: [function (data) {
      let ret = ''
      for (let it in data) {
        ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
      }
      return ret
    }],
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  });
};

export const putRequestJson = (url, params) => {
  return axios({
    method: 'put',
    url: `${base}${url}`,
    data: params,
    contentType:'application/json',
    headers: {
      'Content-Type': 'application/json;charset=utf-8'
    }
  });
};



export const deleteRequest = (url) => {
  return axios({
    method: 'delete',
    url: `${base}${url}`
  });
};

export const deleteRequestJson = (url,params) => {
  return axios({
    method: 'delete',
    url: `${base}${url}`,
    data: params,
    contentType:'application/json',
    headers: {
      'Content-Type': 'application/json;charset=utf-8'
    }
  });
};

export const getRequest = (url) => {
  return axios({
    method: 'get',
    url: `${base}${url}`
  });
}

export const getRequestParams = (url,params) => {
  return axios({
    method: 'get',
    url: `${base}${url}`,
    params:params
  });
}



