import {getRequestParams} from './api'


export function converData(data) {
  if(data == null || data == ''||typeof(data)=="undefined" ) return null;
  return data;
}

export  function formatMonthAndDay(data) {
  if(data.length<2) return '0'+data;
  return data;
}



export const initMenu = (router, store)=> {
  if (store.state.routes.length > 0) {
    return;
  }
  console.log( store.state.user.name);
  getRequestParams("/aut/resource/module",{
    username: store.state.user.name,
    projectId:store.state.user.projectId
  }).then(resp=> {
    if (resp && resp.data.code == 200) {
      const fmtRoutes = formatRoutes(resp.data.msg);
      router.addRoutes(fmtRoutes);
      store.commit('initMenu', fmtRoutes);
    }
  })
}
export const formatRoutes = (routes)=> {
  let fmRoutes = [];
  routes.forEach(router=> {
    let {
      name,
      path,
      meta,
      component,
      children
    } = router;
    if (children && children instanceof Array) {
      children = formatRoutes(children);
    }
    let fmRouter = {
      path: path,
      component(resolve){
        if (component.startsWith("Home")) {
          require(['../components/' + component+ '.vue'], resolve)
        }else if (component.startsWith("Pa")) {
          require(['../components/patient/' + component + '.vue'], resolve)
        }else if (component.startsWith("Emp")) {
          require(['../components/emp/' + component + '.vue'], resolve)
        }else if (component.startsWith("Fin")) {
          require(['../components/finance/' + component + '.vue'], resolve)
        }else if (component.startsWith("Arr")) {
          require(['../components/arrangement/' + component + '.vue'], resolve)
        }else if (component.startsWith("Work")) {
          require(['../components/work/' + component + '.vue'], resolve)
        }else if (component.startsWith("Sys")) {
          require(['../components/system/' + component + '.vue'], resolve)
        }else if (component.startsWith("Set")) {
          require(['../components/setup/' + component + '.vue'], resolve)
        }

      },
      name: name,
      meta: meta,
      children: children
    };
    fmRoutes.push(fmRouter);
  })
  return fmRoutes;
}

export const formatData = (data) =>{
  return converData(data);
}



