<template>
  <div class="bobydiv">

    <div class="second_body">
      <div class="logo">
        <!--<img src="images/school_logo.png" alt="" />-->
      </div>
      <div class="stethoscope">
        <img src="../assets/stethoscope.png"/>
      </div>
      <div class="title-zh">
        广州市安裕医疗管理平台
      </div>
      <div class="title-en">
        Health Care Management Platform
      </div>
      <div style="width:250px;margin-top: -165px;margin-left: -25px;">
        <el-form class="login-container" style="width: 100%" label-position="left" label-width="0px"
                 v-loading="loading">
          <el-form-item style="margin-bottom:8px">
            <el-input type="text" v-model="loginForm.username" auto-complete="off"
                      placeholder="账号"></el-input>
          </el-form-item>
          <el-form-item style="margin-bottom:8px">
            <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="密码"></el-input>
          </el-form-item>
          <el-form-item prop="checkPass" style="margin-bottom:8px">
            <el-select v-model="comp.id" style="width: 100%;" @change="setProjectName" placeholder="所属项目" @focus="userBlur">
              <el-option v-for="item in compList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item style="width: 100%;margin-bottom:8px">
            <el-button type="primary" style="width: 50%" @click="submitClick">登录</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

  </div>

</template>

<script>
  export default {
    data() {
      return {
        checked: true,
        loginForm: {
          username: '',
          password: ''
        },
        loading: false,
        comp: {
          id: '',
          name: ''
        },
        compList: []
      }
    },
    methods: {
      submitClick: function () {
        const _this = this;
        this.loading = true;
        this.postRequest('/aut/token', {
          username: this.loginForm.username,
          password: this.loginForm.password,
          projectId: this.comp.id
        }).then(resp => {
          _this.loading = false;
          if (resp && resp.data.code === "200") {
            const data = resp.data;
            const Userdata = {
              name: this.loginForm.username,
              username: this.loginForm.username,
              tokens: data.msg,
              projectName: this.comp.name,
              projectId:this.comp.id
            };
            _this.$store.commit('login', Userdata);
            const path = _this.$route.query.redirect;
            _this.$router.replace({
              path: path === '/' || path === undefined ? '/home' : path
            });
          }else{
            _this.$message.error({
              type: 'fail',
              message: resp.data.msg
            });
          }
        });
      },
      userBlur: function () {
        this.getRequestParams('/aut/association/project', {
          username: this.loginForm.username,
        }).then(resp => {
          if (resp && resp.data.code === "200") {
            const data = resp.data;
            this.compList = data.msg;
          }
        });
      },

      setProjectName(data) {
        let this1 = this
        this.compList.forEach(function (target) {
          if (target.id == data)
            this1.comp.name = target.name;
        })

      }

    }
  }
</script>

<style>
  .login-container {
    /* border-radius: 15px;*/
    background-clip: padding-box;
    margin: 130px auto;
    width: 560px;
    padding: 35px 35px 15px 35px;
    /*  background: #ffc;*/
    /*	background: url(../../build/login_panel.png);*/
    /*  border: 1px solid #eaeaea;*/
    /* box-shadow: 0 0 25px #cac6c6;*/
  }

  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }

  .login_remember {
    margin: 0px 0px 35px 0px;
    text-align: left;
  }

  .second_body {
    margin: auto;
    width: 330px;
    height: 243px;
    margin-top: 10%;
    padding-left: 300px;
    padding-top: 110px;
    background: url(../assets/login_panel.png) no-repeat;
    position: relative;
  }

  .bobydiv {
    margin-top: -60px;
    margin-left: -8px;
    height: 100%;
    position: fixed;
    width: 100%;
    background: url(../assets/login_bg.jpg)
  }

  .stethoscope {
    position: absolute;
    left: 490px;
    top: 270px;
  }

  .title-zh {
    position: absolute;
    top: 50px;
    left: 280px;
    font-size: 26px;
    font-weight: 900;
    color: #0074a6;
  }

  .title-en {
    position: absolute;
    top: 85px;
    left: 300px;
    color: #9aa5a9;
  }

  .logo {
    position: absolute;
    left: 50px;
    top: 30px;
  }
</style>



