import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import store from './store'
import {getRequest} from './utils/api'
import {postRequest} from './utils/api'
import {deleteRequest} from './utils/api'
import {putRequest} from './utils/api'
import {getRequestParams} from './utils/api'
import {initMenu} from './utils/utils'
import {postRequestJson} from './utils/api'
import {putRequestJson} from './utils/api'
import {converData} from "./utils/utils";
import {deleteRequestJson} from "./utils/api"
import {postRequestFile} from "./utils/api"
import './utils/filter_utils'
import 'font-awesome/css/font-awesome.min.css'


Vue.config.productionTip = false;
Vue.use(ElementUI);

Vue.prototype.getRequest = getRequest;
Vue.prototype.postRequest = postRequest;
Vue.prototype.deleteRequest = deleteRequest;
Vue.prototype.putRequest = putRequest;
Vue.prototype.getRequestParams = getRequestParams;
Vue.prototype.postRequestJson=postRequestJson;
Vue.prototype.putRequestJson=putRequestJson;
Vue.prototype.deleteRequestJson = deleteRequestJson
Vue.prototype.postRequestFile = postRequestFile


router.beforeEach((to, from, next)=> {
    if (to.name === 'Login') {
      next();
      return;
    }
  const name = store.state.user.name;
  if (name === '未登录') {
      if (to.meta.requireAuth || to.name == null) {
        next({path: '/', query: {redirect: to.path}})
      } else {
        next();
      }
    }else {

      initMenu(router, store);
       next();
    }
  }
);




new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: {App}
});




